@import url('https://fonts.googleapis.com/css2?family=Leckerli+One&display=swap');
/*Caveat*/
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&display=swap');


@media screen and (max-width: 850px) {
    .navbar *{
        text-decoration: none;
        list-style: none;
        font-family: 'Caveat','I_Ngaan';
        color: #000;
        z-index: 1;
    }
    header{
        background-color: #85B2E0;
        text-align: right;
        position: fixed;
        width: 100%;
        top: 0;
        height: 65px;
    }
    .menubtn{
        visibility: visible;
        position: absolute;
        top: 10px;
        right: 30px;
        width: 40px;
    }
    .xin_logo {
        font-family: 'Leckerli One';
        font-size: 45px;
        position: absolute;
        top: 3px;
        left: 30px;
        color: #000;
    }
    .barul1{
        text-align: center;
        width: 200px;
        height: initial;
        background-color: rgba(133, 178, 224, 0.7);
        border-radius: 20px;
        position: relative;
        margin: 0;
        position: absolute;
        right: 0;
        z-index: -1;
        font-size: 24px;
        padding-top: 65px;
        padding-left: 0;
        visibility: hidden;
    }
    .barul1 ul{
        margin: 0;
        padding: 0;
    }
    .barul1 li{
        margin: 0;
        padding: 0;
    }
    .barul1 >li{
        margin: 0;
        line-height: 35px;
    }
    .barul1 li>ul>li:hover{
        background-color: #85B2E0;
        border-radius: 20px;
    }
    .barul1 li>ul>li{
        line-height: 35px;
        font-size: 20px;
    }
    .barul1 a{
        width: 100%;
        height: 100%;
        display: block;
    }
    .barul1 li:hover{
        background-color: #0080FF;
    }
    .barul1 a:hover{
        color: #FFFFFF;
    }
    .barul1 li:hover >a{
        color: #FFFFFF;
    }
    .hightset{
        height: 60px;
    }
    .barul1>li{
        border-top: #000 1px solid;
        padding-bottom: 5px;
        border-radius: 20px;
    }
    .barul1>li:first-child{
        border-top: #000 0px solid;
        margin-bottom: 0px;
        padding-bottom: 0;
    }
}
@media screen and (min-width: 850px) {
    .menubtn{
        visibility: hidden;
        position: absolute;
        top: -100px;
        right: 30px;
        width: 40px;
    }
    .navbar *{
        text-decoration: none;
        list-style: none;
        font-family: 'Caveat','I_Ngaan';
        color: #000;
    }
    
    .xin_logo {
        font-family: 'Leckerli One';
        font-size: 45px;
        position: absolute;
        top: 3px;
        left: 30px;
    }
    .barul1{
        margin-right: 30px;
        margin-top: 10px;
        display: inline-block;
        font-size: 20px;
    }
    
    header{
        background-color: #85B2E0;
        text-align: right;
        position: fixed;
        width: 100%;
        top: 0;
        height: 65px;
    }

    .barul1>li{
        position: relative;
        float: left;
        text-align: center;
        width: 100px;
        white-space: nowrap;
        color: #000;
        visibility: visible;
    }
    
    .barul1 li{
        border-radius: 20px;
        text-decoration: none;
        line-height: 30px;
        text-align: center;
        padding: 8px 0px;
        width: 90px;
        color: #000;
    }
    
    .barul1 li:hover{
        background-color: #0080FF;
        
    }
    .barul1 a:hover{
        color: #FFFFFF;
    }
    .barul1 li:hover >a{
        color: #FFFFFF;
    }
    
    .barul1 a{
        width: 100%;
        height: 100%;
        display: block;
    }
    
    .barul1 li>ul>li{
        border: #000 0px solid;
        width: 95px;
        background-color: rgba(255, 255, 255, 0.5);
        border-radius: 10px;
        border-bottom: 1px solid #000;
        margin: 0 0 0 -40px;
    }
    .barul1 ul {
        border: #ccc 0px solid;
        border-radius: 20px;
        position: absolute;
        z-index: 99;
        top: 100%;
        left: -1px;
        display: none;
        text-align: center;
        /*background-color: rgba(255, 255, 255, 0.5);*/
        font-size: 18px;
        font-weight: 400;
        color: #000;
    }
    .barul1 li:hover>ul{
        display: block;
        opacity: 1;
        transform: opacity 0.3s;
        left: -1px;
        border-right: 5px;
        
    }
    .hightset{
        height: 60px;
    }
}



